import Reace ,{ Component } from 'react'
import Head from 'next/head'
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem } from 'reactstrap';

class NavbarMain extends Component {
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
          isOpen: false
        };
    }
    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }
    render () {
        const nav = {
            height : '100px',
            position: 'relative',
            width: '100%',
            background: 'rgba(0, 0, 0, 0.63)',
            borderBottom: '2px solid rgba(100, 89, 255, 0.466)',
            color: '#fff'
        }
        const navA = {
            color: '#fff'
        }
        const menu = {
            color: '#fff',
            fontSize: '16px'
        }
        return (
            <div>
                <Navbar light expand="md" style={nav}>
                    <div className="container">
                        <NavbarBrand href="/" style={navA}><h2>Hotel</h2></NavbarBrand>
                        <NavbarToggler onClick={this.toggle} />
                        <Collapse isOpen={this.state.isOpen} navbar>
                            <Nav className="mx-auto" navbar>
                                <NavItem>
                                    <NavLink className="text-white" href="/components/">Home</NavLink>
                                </NavItem>
                                <UncontrolledDropdown nav inNavbar>
                                    <DropdownToggle className="text-white" nav caret>Options</DropdownToggle>
                                    <DropdownMenu right>
                                        <DropdownItem className="text-white">
                                            Option 1
                                        </DropdownItem>
                                        <DropdownItem className="text-white">
                                            Option 2
                                        </DropdownItem>
                                        <DropdownItem divider />
                                        <DropdownItem className="text-white">
                                            Reset
                                        </DropdownItem>
                                    </DropdownMenu>
                                </UncontrolledDropdown>
                            </Nav>
                        </Collapse>
                    </div>
                </Navbar>
            </div>
        )
    }
}



export default NavbarMain